 width: 15px;\n            }\n\n            td.actionmenu-container {\n                a {\n                    white-space: nowrap;\n\n                    &,\n                    &:hover {\n                        text-decoration: none;\n                    }\n                }\n\n                .btn {\n                    color: var(--bs-link-color);\n                    padding: 0.25rem 0.2rem;\n                }\n            }\n        }\n\n        th,\n        td {\n            &:first-child {\n                border-left-width: 1px;\n            }\n\n            padding: 8px;\n        }\n\n        .fc-breadcrumbs-container {\n            .fc-breadcrumbs {\n                font-size: 15px;\n                padding-top: 2px;\n            }\n        }\n    }\n\n    .contextmenu {\n        display: none;\n        position: fixed;\n        z-index: 10000;\n\n        ul {\n            display: block;\n            position: static;\n            margin-bottom: 5px;\n        }\n    }\n\n    .contextmenuactive {\n        outline: 1px dashed orange;\n    }\n\n    .fc-status {\n        display: flex;\n        align-items: center;\n        margin-bottom: 1em;\n        padding: 0.5em;\n\n        .fc-status-text {\n            flex-grow: 1;\n        }\n\n        .btn {\n            margin-left: 1em;\n        }\n    }\n\n    .upload-container .upload-area {\n        // Why is this pinned?\n        // It's make no sense, it breaks the Text of Upload Button\n        // width: 500px;\n        width: auto;\n    }\n\n    li.disabled a {\n        pointer-events: none;\n    }\n\n    @media all and (max-width: 522px) {\n        .navbar #filter {\n            float: none;\n            width: 100%;\n        }\n        #btn-attribute-columns {\n            display: none;\n        }\n        table {\n            thead {\n                th {\n                    display: none;\n                }\n\n                th.selection,\n                th.title,\n                th.actions {\n 